.indigo-login-alert {
	padding: var(--wp--custom--spacing--unit);
	margin-bottom: var(--wp--custom--spacing--unit);
	background-color: rgba(0,0,0,0.4);
	text-align: center;
}


hr.simple-line {
	border-width: 0;
	border-top: 1px solid rgba(255,255,255,0.5);
}


.login-form {
	&-container {
		input {
			margin-bottom: 0;
		}
		h4 {
			margin-bottom: 1rem;
		}
	}
}


.form-cols {
	display: flex;
	flex-direction: row;
	.form-col {
		width: calc(50% - .5rem);
		margin-right: .5rem;
		&:last-of-type {
			width: 50%;
			margin-right: 0;
		}
	}
}
