
.site {
	width: var(--content-width, $container-default-width) !important;
	max-width: 100%;
	margin: 0 auto;
	&-main {
		display: flex;
		align-items: center;
		flex-direction: column;
		article, #comments {
			width: 100%;
		}
	}
	&-content-wrapper {
		transition: opacity 400ms linear;
		.site-loading &, .css-loading & {
			opacity: 0;
		}
	}
}

#tertiary {
	.widget-area {
		display: flex;
		flex-wrap: wrap;
		.widget {
			width: 100%;
			flex-basis: auto; /* default value */
			flex-grow: 1;
		}
		> div, > section, > aside {
			flex: 1;
		}
	}
}
